/* vim: set ft=stylus: */

@media (min-width: 576px) {

.Main {
    -webkit-transition: all 500ms;
    transition: all 500ms;
    min-height: 100%;
    position: relative

}
    .Main .PageAnim-Show {
      min-height: 100%;
      position: absolute;
      left: 50%;
      -webkit-transform: translateX(-50%);
              transform: translateX(-50%);
    }
  }

.Main .PageAnim-Show_key_home {
    height: 100%;
  }

/* Home page (full screen)*/

.Main_mode_home {
    border-top-width: 0;
    height: 100%;
    width: 100%;
    overflow: hidden;
    position: absolute;
  }

.Main.PageAnim_animating.Main_prevMode_home {
    overflow: hidden;
  }

.Main .PageAnim-Show:not(.PageAnim-Show_key_home) {
    /* @mixin debug;*/
    margin-right: auto;
    margin-left: auto;
    border: 30px solid transparent;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    /* @media (min-width: $(breakpoint_lg)px) {*/
    /*   max-width: calc($breakpoint_lg - $pagePadding * 2)px;*/
    /* }*/
    /* @media (min-width: $(breakpoint_xl)px) {*/
    /*   max-width: calc($breakpoint_xl - $pagePadding * 2)px;*/
    /* }*/
  }

@media (min-width: 576px) {

.Main .PageAnim-Show:not(.PageAnim-Show_key_home) {
      max-width: 516px;
      padding-top: 100px
  }
    }

@media (min-width: 768px) {

.Main .PageAnim-Show:not(.PageAnim-Show_key_home) {
      max-width: 708px
  }
    }

/* Home Container*/

.Main .PageAnim-Show_key_home.PageAnim-Show_exiting {
    border: 1px solid green;
    height: 100%;
  }

@media (max-width: 575.98px) {

.Main .PageAnim-Show_key_home.PageAnim-Show_exiting {
      display: none
  }
    }



