/* vim: set ft=stylus: */
.Keywords {
  display: table;
  width: 100%;
  margin-top: 1em;
  margin-bottom: 1em;
}
.Keywords-Title,
  .Keywords-List {
    display: table-cell;
  }
.Keywords-Title {
    white-space: nowrap;
    opacity: .5;
  }
.Keywords-Title:after {
      display: inline-block;
      content: ': ';
    }
.Keywords-List {
    list-style-type: none;
    padding-left: 10px;
    margin: 0;
  }



