/* vim: set ft=stylus: */
.HomePage-AetherCanvas {
    position: absolute;
    left:   0;
    right:  0;
    top:    0;
    bottom: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    -webkit-transition: opacity 1s;
    transition: opacity 1s;
  }
.HomePage-AetherCanvas_ready {
      opacity: 1;
    }



